.sub {
  top: 0;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  opacity: 0;
  transition: all 250ms ease-in-out;
}

.sub:hover {
  opacity: 1;
}

@media only screen and (max-width: 1200px) {
  .grid-container {
    height: 100%;
    display: flex;
    flex-direction: column;
  }
}
